<template>
  <div class="step-wrap" id="step2">
      <div class="step-header-wrap">
        <h2>案件基本信息</h2>
      </div>
      <div class="step-body-without-tabs-wrap"  v-if="contentForm">
        <el-form ref="form" :label-position="labelPosition" :model="contentForm" label-width="80px">
        <el-row>
          <el-col :span="8" style="padding-right:40px;">
            <div class="">
              <el-form-item label="是否共同犯罪">
                <el-select v-model="contentForm.caseJointOffense" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="是否有自首情节">
                <el-select v-model="contentForm.caseVoluntarySurrender" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>
            </div>
          </el-col>

          <el-col :span="8" style="padding:0 20px;">
            <div class="">
              <el-form-item label="是否立案监督案件">
                <el-select v-model="contentForm.caseSupervise" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="是否坦白">
                <el-select v-model="contentForm.caseConfess" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>
            </div>
          </el-col>

          <el-col :span="8"  style="padding-left:40px;">
            <div class="">
              <el-form-item label="是否有立功情节">
                <el-select v-model="contentForm.caseMeritoriousPlot" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="是否追捕">
                <el-select v-model="contentForm.caseChase" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>
            </div>
          </el-col>
        </el-row>

        <el-row>
          <h3 class="h3-title" style="margin-bottom:5px;">
            <p style="font-size:14px;color:#333;font-weight:400;margin-bottom:0;"> 发案、立案、破案经过</p>
            <!-- <small style="font-size:12px;color:#999;font-weight:400;margin-bottom:0;">填写完成后点击确定可自动填充上方案件基本信息</small> -->
            <span class="btn-wrap" style="display:none;"><el-button size="small" @click="addSubTab()" style="color:#42a0f8">确定</el-button></span>
          </h3>
          <el-form-item label="">
            <el-input type="textarea" maxlength="2048" rows="10" v-model="contentForm.caseProcess" style="font-size:14px;color:#999;margin-bottom:10px;"></el-input>
          </el-form-item>
        </el-row>
        </el-form>
      </div>
      <div class="step-footer-wrap">
      <el-button size="small" @click="onSubmit()">保存</el-button>
      <el-button size="small" @click="prev()">上一步</el-button>
      <el-button size="small" type="primary" @click="next()">下一步</el-button>
    </div>
  </div>
</template>

<script>
import { getAjaxPromise } from '@/api/admin'
// import _ from 'lodash'
export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  name: 'step2',
  data() {
    return {
      msg: 'step2',
      labelPosition: 'top',
      contentForm: {
        // 是否共同犯罪
        caseJointOffense: '否',
        // 是否有自首情节
        caseVoluntarySurrender: '否',
        // 是否立案监督案件
        caseSupervise: '否',
        // 是否坦白
        caseConfess: '否',
        // 是否有立功情节
        caseMeritoriousPlot: '否',
        // 是否追捕
        caseChase: '否',
        // 发案立案破案经过
        caseProcess: ''
      },
      copyContentForm: {
        // 是否共同犯罪
        caseJointOffense: '否',
        // 是否有自首情节
        caseVoluntarySurrender: '否',
        // 是否立案监督案件
        caseSupervise: '否',
        // 是否坦白
        caseConfess: '否',
        // 是否有立功情节
        caseMeritoriousPlot: '否',
        // 是否追捕
        caseChase: '否',
        // 发案立案破案经过
        caseProcess: ''
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      window.checkIsSave = (path, close) => {
        this.next(path, close)
      }
      this.contentForm1 = Object.assign({}, this.contentForm)
      // 获取案件基本信息
      this.getCaseBaseInfo()
    })
  },
  methods: {
    next(path, close) {
      // 判断案件信息是否有修改
      let flag = this.isObjEqual(this.copyContentForm, this.contentForm)
      if (flag) {
        if (close) {
          if (eDossier) {
            eDossier.close()
          }
        } else {
          if (path) {
            this.$router.push({ path: path })
          } else {
            this.$router.push({ name: 'step3' })
          }
        }
      } else {
        this.$confirm('有修改的案件信息未保存, 是否保存?', '', {
          confirmButtonText: '保存',
          cancelButtonText: '取消',
          closeOnClickModal: false,
          center: true,
          showClose: false
        })
          .then(() => {
            this.onSubmit(path, close)
          })
          .catch(() => {
            if (close) {
              if (eDossier) {
                eDossier.close()
              }
            } else {
              if (path) {
                this.$router.push({ path: path })
              } else {
                this.$router.push({ name: 'step3' })
              }
            }
          })
      }
    },
    prev() {
      // 判断案件信息是否有修改
      let flag = this.isObjEqual(this.copyContentForm, this.contentForm)
      if (flag) {
        this.$router.push({ name: 'step1' })
      } else {
        this.$confirm('有修改的案件信息未保存, 是否保存?', '', {
          confirmButtonText: '保存',
          cancelButtonText: '取消',
          closeOnClickModal: false,
          center: true,
          showClose: false
        })
          .then(() => {
            this.onSubmit()
          })
          .catch(() => {
            this.$router.push({ name: 'step1' })
          })
      }
    },
    // 获取案件基本信息
    getCaseBaseInfo() {
      const param = {
        caseNumber: this.caseObj.caseNumber,
        userId: localCache.getItem('userId')
      }
      const baseInfoPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/caseBaseInfo/getCaseBaseInfo',
        headers: {
          contentType: 'application/json'
        },
        data: param
      })
      baseInfoPromise.then(res => {
        if (res.data.data && res.data.data.length > 0) {
          this.contentForm = res.data.data[0]
          if (this.contentForm.caseJointOffense === '') {
            this.contentForm.caseJointOffense = '否'
          }
          if (this.contentForm.caseVoluntarySurrender === '') {
            this.contentForm.caseVoluntarySurrender = '否'
          }
          if (this.contentForm.caseSupervise === '') {
            this.contentForm.caseSupervise = '否'
          }
          if (this.contentForm.caseConfess === '') {
            this.contentForm.caseConfess = '否'
          }
          if (this.contentForm.caseMeritoriousPlot === '') {
            this.contentForm.caseMeritoriousPlot = '否'
          }
          if (this.contentForm.caseChase === '') {
            this.contentForm.caseChase = '否'
          }
          this.copyContentForm = Object.assign({}, this.contentForm)
        } else if (res.data.code === '500') {
          this.$message({
            message: res.data.msg,
            type: 'error',
            duration: 2000
          })
        }
      })
    },
    // 保存案件基本信息
    onSubmit(path, close) {
      if (!localCache.getItem('userId')) {
        this.$message({
          message: 'userId为空',
          type: 'error',
          duration: 2000
        })
        return
      }
      this.contentForm.createUser = localCache.getItem('userId')
      // this.contentForm.updateUser = localCache.getItem('userId')
      const saveOrUpdateCaseBaseInfoPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/caseBaseInfo/saveOrUpdateCaseBaseInfo',
        headers: {
          contentType: 'application/json'
        },
        data: this.contentForm
      })
      saveOrUpdateCaseBaseInfoPromise.then(res => {
        if (res.data.code === '200') {
          this.$message({
            message: '保存案件基本信息成功',
            type: 'success',
            duration: 2000
          })
          this.getCaseBaseInfo()
          if (close) {
            if (eDossier) {
              eDossier.close()
            }
          } else {
            if (path) {
              this.$router.push({ path: path })
            }
          }
        } else {
          this.$message({
            message: res.data.msg,
            type: 'error',
            duration: 3000
          })
        }
      })
    },
    // 数据抽取：即根据发案、立案、破案经过输入框中的内容动态抽取关键信息
    addSubTab() {
      if (
        !this.contentForm.caseProcess ||
        this.contentForm.caseProcess === ''
      ) {
        this.$message({
          message: '发案、立案、破案经过内容不能为空',
          type: 'warning',
          duration: 2000
        })
        return
      }
      const param = {
        caseProcess: this.contentForm.caseProcess
      }
      const saveOrUpdateCaseBaseInfoPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/caseBaseInfo/dataExtraction',
        headers: {
          contentType: 'application/json'
        },
        data: param
      })
      saveOrUpdateCaseBaseInfoPromise.then(res => {
        if (res.data.code === '200') {
          this.$message({
            message: '案件基本信息填充成功',
            type: 'success',
            duration: 2000
          })
        } else {
          this.$message({
            message: '案件基本信息填充失败',
            type: 'error',
            duration: 2000
          })
        }
      })
    },
    isObjEqual(o1, o2) {
      let props = Object.getOwnPropertyNames(this.contentForm1)
      let props1 = Object.getOwnPropertyNames(o1)
      let props2 = Object.getOwnPropertyNames(o2)
      if (props1.length !== props2.length) {
        return false
      }
      for (let i = 0, max = props.length; i < max; i++) {
        let propName = props[i]
        if (o1[propName] !== o2[propName]) {
          return false
        }
      }
      return true
    }
  }
}
</script>
<style lang="scss">
.el-select-dropdown__item {
  span {
    font-size: 14px;
  }
  color: #666;
}
#step2 {
  .el-input {
    width: 90%;
  }
  .el-select {
    width: 100%;
    > .el-input {
      width: 100%;
    }
  }
  .step-header-wrap {
    h2 {
      font-size: 14px;
      color: #333;
      font-weight: 600;
      text-indent: 24px;
    }
  }
  .step-body-without-tabs-wrap {
    padding: 20px 24px;
  }
  .el-form-item__label {
    font-size: 14px;
    color: #333;
    font-weight: 400;
  }

  .el-input {
    width: 100%;
    > .el-input__inner {
      color: #666;
      font-size: 14px;
    }
  }
  .step-footer-wrap {
    height: 64px;
    line-height: 64px;
  }
  .el-select {
    width: 100%;
    > .el-input {
      width: 100%;
    }
  }
  .el-row {
    margin-bottom: 2px;
  }

  .el-form-item {
    margin-bottom: 16px;
  }
  .el-tabs--top {
    .el-tabs__header {
      padding-left: 22px;
    }
  }

  .el-tabs__nav {
    padding: 0 8px;
  }

  .el-tabs__nav {
    padding: 0 8px;
  }

  .el-tabs__active-bar {
    padding: 0 8px;
  }

  .el-tabs__item {
    color: #333;
  }

  .el-tabs__item.is-active {
    color: #42a0f8;
  }

  .el-form-item__label {
    font-size: 14px;
    color: #333;
    padding-bottom: 0;
    font-weight: 400;
    line-height: 1.5;
    margin-bottom: 6px;
  }
}
</style>



